<template>
<div class=" w90p pt48 pb48" style="margin:0 auto;">
    <div class="firstTitle center">碳价计算器</div>
    <div class="flexCenter mt20" style="">
        <div class="formBox " style="width:600px;">
        <Form :model="form">
            <div class="red ">务必确认当前步骤完成后，再顺序执行后续操作!</div>
            <div class="mt10">1.设置约定的交易数量和价格</div>
            <div class="mt10 ml20">约定吨数<Input v-model="form.tons" maxlength="10" placeholder="请输入吨数" class="w120 ml10"></Input></div>
            <div class="mt10 ml20">
                <RadioGroup v-model="form.mode_1" vertical>
                    <Radio label="mode_11">
                        【单价】 单价<Input v-model="form.price" maxlength="10" placeholder="请输入单价" class="w120 ml10"></Input>
                    </Radio>
                    <Radio label="mode_12"  class="mt10">
                        【总价】 总金额<Input v-model="form.total_values" maxlength="10" placeholder="请输入总金额" class="w120 ml10"></Input>
                    </Radio>
                </RadioGroup>
            </div>
            <div class="mt10">2.设置约定的手续费结算方式(三选一)</div>
            <div class="mt10 ml20">手续费为千分之六(默认)<Input v-model="form.fees" maxlength="10" placeholder="0.000" class="w120 ml10"></Input></div>
            <div class="mt10 ml20">
                <RadioGroup v-model="form.mode_2" >
                    <Radio label="mode_21">【企业承担双向】</Radio>
                    <Radio label="mode_22">【各自承担】</Radio>
                    <Radio label="mode_23">【机构承担双向】</Radio>
                </RadioGroup>
            </div>
            <div class="mt10">3.设置手续费扣除方式(二选一)</div>
            <div class="mt10 ml20">
                <RadioGroup v-model="form.mode_3" >
                    <Radio label="mode_31">【单独扣除】</Radio>
                    <Radio label="mode_32">【从交易费用中扣除】</Radio>
                </RadioGroup>
            </div>
            <div class="mt10">4.设置线上可成交的配额价格</div>
            <div class="mt10 ml20">
                SZEA(固定价格)<Input v-model="form.no_1_cur" maxlength="20" placeholder="请输入当前价" class="w200 ml10"></Input>
            </div>
            <div class="mt10">5.是否需要辅助交易品种周转</div>
            <div class="mt10">
                <!-- <RadioGroup v-model="form.yyy" vertical>
                    <Radio label="【单价】 单价">
                        【是】
                        <div>输入辅助品种最低价<Input v-model="form.input" placeholder="请输入价格" class="w300"></Input></div>
                        <div>输入辅助品种数量(上限)<Input v-model="form.input" placeholder="请输入吨数" class="w300"></Input></div>
                        <div>辅助品种(价格浮动范围)<Input v-model="form.input" placeholder="请输入最低价" class="w300"></Input><Input v-model="form.input" placeholder="请输入最高价" class="w300"></Input></div>
                    </Radio>
                    <Radio label="【否】差额费用线下单独转账补齐">
                        【否】差额费用线下单独转账补齐
                    </Radio>
                </RadioGroup> -->
                <Select v-model="form.mode_5" class="ml20" style="width:auto;" placeholder="请选择">
                    <Option value="mode_51">【是】</Option>
                    <Option value="mode_52">【否】 差额费用线下单独转账补齐</Option>
                </Select>
                <div v-if="form.mode_5=='mode_51'" class="ml20">
                    <div  class="mt10">
                        输入辅助品种最低价<Input v-model="form.price_2" maxlength="10" placeholder="请输入价格" class="w120 ml10"></Input>
                    </div>
                    <div class="mt10">
                        输入辅助品种数量(上限)<Input v-model="form.tons_2" maxlength="10" placeholder="请输入吨数" class="w120 ml10"></Input>
                    </div>
                    <div class="mt10">
                        辅助品种(价格浮动范围)
                        <Input v-model="form.no_2_low" maxlength="10" placeholder="请输入最低价" class="w120 ml10"></Input>
                        <Input v-model="form.no_2_high" maxlength="10" placeholder="请输入最高价" class="w120 ml10"></Input>
                    </div>
                </div>
            </div>
            <div class="mt10">6.检查无误后进行计算</div>
            <div class="mt10 ml20">阀值设置(金额误差) <Input v-model="form.gap" maxlength="10" placeholder="0" class="w120 ml10"></Input></div>
            <form class="mt10">
                <Button type="primary" @click="submit" style="min-width:80px;">确认</Button>
                <!-- <Button style="margin-left: 15px;min-width:80px;">重置</Button> -->
            </form>
            <div class="bg0 p10 green mt10" v-html="holding_str">
                <!-- {{ holding_str }} -->
            </div>
        </Form>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: "",
    props: {},
    components: {},
    data() {
        return {
            form: {
                mode_1:'mode_11',
                mode_2:'mode_21',
                mode_3:'mode_31',
                mode_5:'mode_51',
                fees:0.006,
            },
            // form: {
            //     tons:30000,
            //     price:24,
            //     fees:0.006,
            //     no_1_cur:40,
            //     price_2:0.01,
            //     tons_2:5000,
            //     no_2_low:0.01,
            //     no_2_high:100,
            //     gap:20,
            //     mode_1:'mode_11',
            //     mode_2:'mode_21',
            //     mode_3:'mode_31',
            //     mode_5:'mode_51',
            // },
            holding_str:' 请填写内容后，点击确认按钮...',//'计算失败，请检查数字格式'
        };
    },
    mounted() {},
    methods: {
        submit(){
            console.log(221,this.form,this.form.mode_2)
            if(!this.form.tons || !this.form.no_1_cur || !this.form.gap){
                this.holding_str='计算失败，请检查数字格式'
                this.$Message.error(this.holding_str);
                return;
            }
            let flag =0;
            let tons= parseInt(parseFloat(this.form.tons))
            let cur = parseInt(parseFloat(this.form.no_1_cur)*100)
            let gap= parseInt(parseFloat(this.form.gap)*100)

            let best_tons =0
            let best_values=0
            // let best_gap=-this.form.gap  //问题出在这里
            let best_gap=-gap

            //变量
            let fees=0;
            let price=0;
            let total_values=0;

            // 手续费判断计算
            if(this.form.mode_2=='mode_21'){
                fees=parseFloat(this.form.fees)*2;
            }else if(this.form.mode_2=='mode_22'){
                // console.log('进入mode_22')
                fees=parseFloat(this.form.fees);
            }else{
                fees=0
            }
            // 单价或总价处理
            if(this.form.mode_1=='mode_11'){
                price=parseInt(parseFloat(this.form.price)*100);
                total_values=price *tons
            }
            if(this.form.mode_1=='mode_12'){
                total_values =parseInt(parseFloat(this.form.total_values)*100)
                price = total_values/tons
            }
            let fix_value= 0;
            let price_2=0;
            //是否需要辅助交易品种周转。是
            if(this.form.mode_5=='mode_51'){
                price_2 = parseInt(parseFloat(this.form.price_2)*100)
                let tons_2 = parseInt(parseFloat(this.form.tons_2))
                let low = parseInt(parseFloat(this.form.no_2_low)*100)
                let high= parseInt(parseFloat(this.form.no_2_high)*100)
                if(this.form.mode_3=='mode_31'){
                    fix_value = total_values
                }else{
                    fix_value = total_values/(1+fees)
                }
                // console.log('所有值：',price_2,tons_2,low,high,total_values,fix_value,price)
                let tmp=0;
                let t_fees=0
                let count=0;
                for(let j=low;j<high+1;j++){
                    for(let i=1;i<tons_2+1;i++){
                        count++;
                        t_fees = (cur*tons + (j + price_2)*i)*fees  //机构承担 0  企业承担 0.012 
                        
                        if(cur<price){
                            tmp =  total_values - (cur*tons + (j - price_2)*i) + t_fees
                        }else{
                            tmp =  total_values - (cur*tons - (j - price_2)*i) + t_fees
                        }
                        // console.log(tmp,'tmp')
                        if(tmp>0){
                            continue
                        }
                        if(-gap<tmp && tmp<0){
                            flag =1
                            // console.log('进入',-gap,tmp,best_gap,best_gap<tmp)
                            if(best_gap<tmp){
                                // console.log('进入2')
                                best_gap = tmp
                                best_tons = i
                                best_values=j
                            }
                        }
                    }
                }
                // console.log('所有值2：',tmp,t_fees,count,best_gap,best_tons,best_values)
            }
            //是否需要辅助交易品种周转。否
            if(this.form.mode_5=='mode_52'){
                total_values = price *tons
                if(this.form.mode_3=='mode_31'){
                    fix_value = total_values
                }else{
                    fix_value = total_values/(1+fees)
                }
                best_tons = tons
                best_values = price
                flag =2
            }
            function round(val,num){
                return val?val.toFixed(num):val;
            }
            let holding_str=''
            if(flag==2){
                let total = cur*best_tons/100
                let premium = (fix_value - cur*best_tons )/100
                holding_str = `深圳配额SZEA 吨数:${tons} 单价:${round(cur/100,2)}\n `+
                `交易总吨数 ${tons}\n交易收支 ${-round(total,2)}\n`+
                    `产生手续费 ${round(fees*total,2)}\n最终资金变动 ${-round(total*(1+fees),2)}\n`+
                        `线下差额费用 ${premium}`
            }else if(flag==1){
                let total = fix_value/100
                holding_str = `深圳配额SZEA 吨数:${tons} 单价:${round(cur/100,2)}\n`+
                `辅助品种 倒差吨数:${best_tons} 倒差单价:${round(best_values/100,2)} 最低单价:${round(price_2/100,2)}\n`+
                    `交易收支 ${-round(total,2)}\n`+
                        `产生手续费 ${round(fees*total,2)}\n最终资金变动 ${-round(total*(1+fees),2)}`
            }else if(flag==0){
                holding_str ='计算失败，请调整阈值或者调整辅助品种参数'
            }
            console.log('holding_str:',holding_str)
            holding_str=holding_str.replaceAll('\n','<br/>');
            this.holding_str=holding_str;
            return holding_str;
        }
    },
};
</script>

<style scoped lang='less'>
.formBox{
    padding:20px;
        background-color: #1e2026;
    // border:1px solid #434651;
}
</style>